.body {
  padding: 30px;
  /* width: 100%; */
  height: 100vh;
  overflow-y: overlay;
  flex: 0.8;
  color: #000;
  background: #fff;
  /* background: linear-gradient(transparent, rgb(255 120 236));
  background-color: rgb(221 221 221); */
}

.body::-webkit-scrollbar {
  display: none;
}

.player__body {
  width: 60%;
  box-shadow: 1px 4px 8px 3px #e5e2e2;
  border-radius: 10px;
  margin: 5rem auto;
}

.body__info {
  display: flex;
  align-items: flex-end;
  padding: 10px;
}

.body__shuffle {
  font-size: 80px !important;
  margin-left: 50px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.body__icons {
  display: flex;
  align-items: center;
}

.body__icons > .MuiSvgIcon-root {
  margin-right: 20px;
}

.body__shuffle:hover {
  transition: 100ms transform ease-in;
  transform: scale(1.08);
}

.body__background {
  position: absolute;
  top: 13vh;
  right: 0;
  opacity: 0.4;
  height: 80%;
  width: 80%;
  background-color: black;
}

.body__songs {
  z-index: 1;
  margin: 20px -30px;
}

.body__info > img {
  height: 20vw;
  margin: 0 20px;
  box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
}

.body__infoText {
  flex: 1;
}

.body__infoText > h2 {
  font-size: 48px;
  margin-bottom: 10px;
}

.body__infoText > p {
  font-size: 14px;
}
